.web {
    display: flex;
    flex: 1;
    align-items: center;
}

.menu-option{
    font-size: 20px;
    margin-right: 16px;
    font-weight: 700;
}

.menu-option a {
    text-decoration: none;
    color: black;
    display: flex;
    align-items: center;
  }
  
  .option-icon {
    display: flex;
    align-items: center;
    margin-right: 4px;
  }
  
  .menu-option:last-child {
    margin-right: 0px;
  }